const rowSelection = {
  onChange: (selectedRowKeys, selectedRows) => {
    console.log(
      `selectedRowKeys: ${selectedRowKeys}`,
      "selectedRows: ",
      selectedRows
    );
  },
  getCheckboxProps: (record) => ({
    disabled: record.name === "Disabled User",
    name: record.name,
  }),
};

const GridAssignEmp = () => {
  const [selectionType, setSelectionType] = useState("checkbox");
  return (
    <div>
      <Col span={24}>
        <Row className="pt-1 pb-3">
          <Col span={18} className="search-row">
            <Row>
              <Col span={9} className="search-box">
                <Search placeholder="Search" onSearch={onSearch} />
              </Col>
            </Row>
          </Col>
        </Row>
      </Col>
      <AssignHotlineModal />
      <Table
        rowSelection={{
          type: selectionType,
          ...rowSelection,
        }}
        columns={columns}
        dataSource={data}
      />
    </div>
  );
};

export default GridAssignEmp;
