import React, { Component } from "react";
import { Layout, Row, Col, Input, Typography, Badge, Divider } from "antd";
import { LoggedinHeader } from "../../containers";
import "./hotlineMessages.scss";
import AmWebSidebar from "./AmWebSidebar";
import FilterPopover from "./FilterPopover";
import axios from "axios";
import moment from "moment";
import authHeader from "../../services/auth-header";
import Messages from "./Messages";
import { BASE_URL } from "../../userconfig";
import {Scrollbars} from "react-custom-scrollbars"; 
import * as AppStorage from '../../utils/AppStorage';

const { Paragraph } = Typography;
const { Search } = Input;
const { Content } = Layout;

class HotlineMessages extends Component {
  constructor() {
    super();
    this.state = {
      conversations: [],
      conversation: "",
      urgencyLevel: "",
      status: "",
    };
    this.onConversationClick = this.onConversationClick.bind(this);
  }

  componentDidMount() {
   this.getConversations();
  }

  getConversations = () =>{
    const token = authHeader();
    const user = JSON.parse(AppStorage.get("user"));
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };

    axios
      .get(
        BASE_URL +
          "hotlineResponderConversation/?user_id=" +
          user.user_details.org_user_id,
        options
      )
      .then((res) => {
        this.setState({
          conversations: res.data,
        });
      });
  }

  onConversationClick = (conversation) => {
    this.setState({
      conversation: conversation,
      urgencyLevel: conversation.urgency_level.urgency_level_id,
      status: conversation.status.status_id,
    });
  };

  onChangeUrgencyLevel = (event, conversation_id) => {
    const token = authHeader();
    const user = JSON.parse(AppStorage.get("user"));
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };

    const data = {
      conversation_id: conversation_id,
      updated_by: user.user_details.org_user_id,
      urgency_level_id: event,
    };

    axios
      .put(BASE_URL + "hotlineConvoMessagesUrgencyLevel/ ", data, options)
      .then((res) => {
        if(res.status===200){
          this.getConversations();
          this.setState({urgencyLevel : event})
        }
      });
  };

  onChangeStatus = (event, conversation_id) => {
    const token = authHeader();
    const user = JSON.parse(AppStorage.get("user"));
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };

    const data = {
      conversation_id: conversation_id,
      updated_by: user.user_details.org_user_id,
      status_id: event,
    };

    axios
      .put(BASE_URL + "hotlineConvoMessagesStatus/", data, options)
      .then((res) => {
        if(res.status===200){
          this.getConversations();
          this.setState({status : event})
        }
      });
  };

  render() {
    return (
      <>
        <Row className="inner-header">
        <LoggedinHeader amWEBUser/>
        </Row>
        <Row className="content-center pt-0">
          <Layout>
            <Layout>
            <AmWebSidebar amWEBUser/>
              <Content className="">
                <Row className="py-3">
                  <h1 className="mb-0">Hotline Messages</h1>
                </Row>
                <div className="mid-content p-0">
                  <Row>
                    <Col span={6} className="message-section py-4">
                      <Col
                        span={24}
                        className=" d-flex justify-content-between px-3"
                      >
                        <h6>Messages</h6>
                        <FilterPopover />
                      </Col>

                      {/* <Col span={24} className="mt-4 px-3 d-block">
                        <Tag closable onClose={log} className="mb-2">
                          Urgency : <span>High</span>
                        </Tag>
                        <Tag closable onClose={log} className="mb-2">
                          Hotline : <span>Microagressions </span>
                        </Tag>
                        <Tag closable onClose={log} className="mb-2">
                          Hotline :{" "}
                          <span>
                            Microagressions asjdsdhkjashdkhsjkdhaskjdhkjashdjk{" "}
                          </span>
                        </Tag>
                      </Col> */}
                      <Col span={24} className="search-box mt-3 px-3">
                        <Search placeholder="Search" onSearch={this.onSearch} />
                      </Col>
                      <Scrollbars
                        onScroll={this.handleScroll}
                        onScrollFrame={this.handleScrollFrame}
                        onScrollStart={this.handleScrollStart}
                        onScrollStop={this.handleScrollStop}
                        onUpdate={this.handleUpdate}
                        renderView={this.renderView}
                        renderTrackHorizontal={this.renderTrackHorizontal}
                        renderTrackVertical={this.renderTrackVertical}
                        renderThumbHorizontal={this.renderThumbHorizontal}
                        renderThumbVertical={this.renderThumbVertical}
                        autoHeight
                        autoHeightMin={0}
                        autoHeightMax={450}
                        thumbMinSize={30}
                        universal={true}
                      >
                      <Col span={24} className="mt-4 inbox-messages">
                        {this.state.conversations.map((item) => (
                          <div
                            className="msg-wrapper"
                            onClick={() => this.onConversationClick(item)}
                          >
                            <div className="d-flex justify-content-between">
                              <Paragraph ellipsis className="w-75 pr-2 mb-1">
                                {item.conversation_name}
                              </Paragraph>

                              <span className="small">
                                {" "}
                                {moment(item.created_on).isSame(moment(), "day")
                                  ? moment(item.created_on).format("hh:mm")
                                  : moment(item.created_on).format("MM/DD")}
                              </span>
                            </div>
                            <div className="d-flex justify-content-between">
                              <Paragraph
                                ellipsis
                                className="font-weight-normal mb-1 sm"
                              >
                                {item.hotline.name} | {item.conversation_id}
                              </Paragraph>
                              <span className="small">
                                {" "}
                                <Badge count={item.unread_count} />
                              </span>
                            </div>
                          </div>
                        ))}
                      </Col>
                      </Scrollbars>
                    </Col>

                    {this.state.conversation && (
                      <Messages
                        conversation={this.state.conversation}
                        urgencyLevel={this.state.urgencyLevel}
                        status={this.state.status}
                        onChangeUrgencyLevel={this.onChangeUrgencyLevel}
                        onChangeStatus={this.onChangeStatus}
                      />
                    )}
                  </Row>
                </div>
              </Content>
            </Layout>
          </Layout>
        </Row>
      </>
    );
  }
}

export default HotlineMessages;
