import React from "react";
import { Form, Select, Input, Button, DatePicker } from "antd";
import moment from "moment";

import AddIcoModal from "../../components/modal/AddIcoModal";
import "../form/form.scss";
const { TextArea } = Input;
const { Option } = Select;

const normFile = (e) => {
  console.log("Upload event:", e);

  if (Array.isArray(e)) {
    return e;
  }

  return e && e.fileList;
};

const Cform = () => {
  const onFinish = (values) => {};

  return (
    <Form className="input-md w-75" name="validate_other" onFinish={onFinish}>
      <Form.Item
        name="directory text"
        rules={[
          {
            required: true,
            message: "Please enter directory name ",
          },
        ]}
      >
        <TextArea
          showCount
          maxLength={80}
          className="input-control textarea-input"
          placeholder="Directory Name"
          htmlFor="directory text"
        />
      </Form.Item>
      <Form.Item
        name="description"
        rules={[
          {
            required: true,
            message: "Please enter description",
          },
        ]}
      >
        <TextArea
          className="input-control textarea-md"
          placeholder="Description"
          htmlFor="description"
        />
      </Form.Item>

      <Form.Item
        className="input-control datepicker-wrapper"
        name="sdate"
        rules={[
          {
            required: true,
            message: "Please select date",
          },
        ]}
      >
        <DatePicker className="datepicker w-50 mr-5" />
        <DatePicker className="datepicker w-50" />
      </Form.Item>
      <Form.Item
        name="status"
        rules={[
          {
            required: true,
            message: "Please select status",
          },
        ]}
      >
        <Select
          className="input-control w-50 mr-5"
          placeholder="Status"
          htmlFor="status"
        >
          <Select.Option value="demo">Demo</Select.Option>
        </Select>
      </Form.Item>
      <Form.Item>
        <Form.Item
          className="upload-control"
          name="upload"
          valuePropName="fileList"
          getValueFromEvent={normFile}
        >
          <AddIcoModal />

          <div className="ant-upload-list ant-upload-list-picture">
            <div className="ant-upload-list-item ant-upload-list-item-list-type-picture">
              <div className="ant-upload-list-item-info d-inline-flex w-100">
                <span className="d-inline-flex align-items-center">
                  <a
                    className="ant-upload-list-item-thumbnail"
                    href=""
                    target="_blank"
                    rel=""
                  >
                    <span className="icon icon-sexual-harassment-1" />
                  </a>

                  <span
                    className="ant-upload-list-item-name ant-upload-list-item-name-icon-count-1"
                    title="women safety"
                  >
                    Women Safety
                  </span>
                  <span className="uploaded-icon icon icon-close d-inline-flex justify-content-end align-items-center"></span>
                </span>
              </div>
            </div>
          </div>
        </Form.Item>
      </Form.Item>
      <Form.Item>
        <Button type="primary" htmlType="submit">
          Next
        </Button>
      </Form.Item>
    </Form>
  );
};

export default Cform;
