import React, { Component } from "react";
import { Row, Col, Button, Divider, Tag } from "antd";

class Assigned extends Component {
  constructor() {
    super();
    this.state = {
      myclass: "",
    };
    this.divclicked = this.divclicked.bind(this);
  }

  divclicked() {
    if (this.state.myclass === "") {
      this.setState({
        myclass: "rgt-panel",
      });
    } else {
      this.setState({
        myclass: "",
      });
    }
  }

  render() {
    return (
      <>
        <span onClick={this.divclicked} className="icon icon-create"></span>
        <div id="rgt-panel" className={this.state.myclass}>
          <p>
            <Row>
              <Col span={24}>
                <Row className="filter-wrapper align-items-center justify-content-between px-3 pt-3">
                  <Col span="12">
                    <h6>Assigned To</h6>
                  </Col>
                  <Button
                    onClick={this.showModal}
                    type="link"
                    className="link pl-0 pt-0 align-items-center d-flex"
                  >
                    <span className="icon icon-close small" />
                  </Button>
                </Row>
                <Divider className="my-2 w-100" />
                <Col span="24" className="px-3 pt-2 assignees d-flex flex-column flex-gap">
                <Tag>Jake Knapp</Tag>
                <Tag>Kierra Siphronlongnamelongnamelongname</Tag>
                <Tag>Cooper Schleifer</Tag>
                </Col>
              </Col>
            </Row>
          </p>
        </div>
      </>
    );
  }
}

export default Assigned;
