import React from "react";
import { Layout, Steps, Button, message, Row, Col, Breadcrumb } from "antd";
import { LoggedinHeader } from "../../containers";
import Buttons from "../button/Button";
import "../hotlineCreation/hotlineCreation.scss";
import Cform from "../form/CreateHotlineForm";
import { connect } from "react-redux";
import CreateHotlineStepOne from "../form/CreateHotlineStepOne";
import CreateHotlineStepTwo from "../form/CreateHotlineStepTwo";
import CreateHotlineStepThree from "../form/CreateHotlineStepThree";
import GridAssignEmp from "../../components/table/TableAssignEmp";
import moment from "moment";
import authHeader from "../../services/auth-header";
import axios from "axios";
import { Link, NavLink } from "react-router-dom";
import OrgSidebar from "../../containers/OrgSidebar";
import * as AppStorage from '../../utils/AppStorage';

const { Content } = Layout;

const { Step } = Steps;

const steps = [
  {
    title: "Add Details",
    content: "First-content",
  },
  {
    title: "Assign Employee",
    content: "Second-content",
  },
  {
    title: "Create Directory",
    content: "Last-content",
  },
];

class HotlineCreation extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      current: 0,
      stepOneHotLine: "",
      assingUserIds: [],
      assignUserName: "",
      canViewReports: 0,
      canRespond: 0,
      permissions:[]
    };
  }
  next = () => {
    const { current } = this.state;
    this.setState({ current: current + 1 });
  };

  prev = () => {
    const { current } = this.state;
    this.setState({ current: current - 1 });
  };

  getStepOneValue = (hotLinedata) => {
    console.log("hotlineForm", hotLinedata);
    this.setState({
      stepOneHotLine: hotLinedata,
    });
  };

  getStepTwoValue = (selectedRow, checkedValues) => {
    console.log("step two", selectedRow);
    console.log("step checkedValues", checkedValues);
    

    const newArray = selectedRow.map((element) => element.user_id);
    const assignedUserNames = selectedRow.map(
      (element) => element.firstname + " " + element.lastname
    );

    let per=[]
    checkedValues.map((item) =>
      per.push(item)
    )
    this.setState({
      assingUserIds: newArray,
      assignUserName: assignedUserNames.join(", "),
      permissions:per
    });
  };

  createHotLine = (values) => {
    const user = JSON.parse(AppStorage.get("user"));
    // var data = [];
    // data.push({
    //   name: values.directoryName,
    //   help_text: values.description,
    //   start_date: values.sDate ? moment(values.sDate).format("DD-MM-YYYY") : "",
    //   end_date: values.tDate ? moment(values.tDate).format("DD-MM-YYYY") : "",
    //   hotline_icon_id: parseInt(values.hotlineIconId),
    //   assign_user_ids: this.state.assingUserIds,
    //   can_respond: this.state.canRespond,
    //   can_view_reports: this.state.canViewReports,
    //   created_by: user.user_details.org_user_id,
    // });
    const token = authHeader();

    const data = {
      create_hotline: {
        name: values.directoryName,
        help_text: values.description,
        start_date: values.sDate ? moment(values.sDate).format("DD-MM-YYYY") : "",
        end_date: values.tDate ? moment(values.tDate).format("DD-MM-YYYY") : "",
        status_id:values.status,
        hotline_icon_id: parseInt(values.hotlineIconId),
        created_by: user.user_details.org_user_id
       },
       permissions:this.state.permissions
      }

    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    console.log("values final", data);
    axios
      .post("http://10.0.6.10:8000/api/V1/hotline/", data, options)
      .then((res) => {
        console.log(res);
        console.log(res.data);
        window.location = "/displayhotlines";
      });
  };

  render() {
    const { current, stepOneHotLine, assignUserName } = this.state;
    return (
      <>{console.log("dghsd : ", this.state.permissions)}
        <Row className="inner-header">
          <LoggedinHeader amORGUser/>
        </Row>
        <Row className="content-center pt-0">
          <Layout>
            <Layout>
            <OrgSidebar amORGUser/>
              <Content className="">
                <Row className="pt-3">
                  <Breadcrumb separator=">">
                  <NavLink to="/displayhotlines">
                      <Breadcrumb.Item>Hotline Directory</Breadcrumb.Item>
                    </NavLink>
                    <Breadcrumb.Item>Create Hotline Directory</Breadcrumb.Item>
                  </Breadcrumb>
                </Row>
                <Row className="py-3">
                  <h1 className="mb-0">Create Hotline Directory Entries</h1>
                </Row>
                <Steps className="mt-2" current={current}>
                  {steps.map((item) => (
                    <Step key={item.title} title={item.title} />
                  ))}
                </Steps>
                <div className="mid-content mt-4">
                  <Row>
                    <Col span={24}>
                      <div className="steps-content">
                        {current == 0 ? (
                          <CreateHotlineStepOne
                            next={this.next}
                            getStepOneValue={this.getStepOneValue}
                          />
                        ) : current == 1 ? (
                          <CreateHotlineStepTwo
                            next={this.next}
                            prev={this.prev}
                            getStepTwoValue={this.getStepTwoValue}
                          />
                        ) : current == 2 ? (
                          <CreateHotlineStepThree
                            prev={this.prev}
                            hotLineValues={stepOneHotLine}
                            createNewHotLine={this.createHotLine}
                            userNames={assignUserName}
                          />
                        ) : (
                          ""
                        )}
                        {/* <Cform />
                        <Grid /> */}
                      </div>
                      {/* <div className="steps-action mt-5">
                        {current > 0 && (
                          <Button
                            className="mr-4"
                            type="secondary"
                            style={{ margin: "0 8px" }}
                            onClick={() => this.prev()}
                          >
                            Back
                          </Button>
                        )}
                        {current < steps.length - 1 && (
                          <Button
                            className="md"
                            type="primary"
                            onClick={() => this.next()}
                          >
                            Next
                          </Button>
                        )}
                        {current === steps.length - 1 && (
                          <Button
                            type="primary"
                            onClick={() => message.success("Created")}
                          >
                            Create
                          </Button>
                        )}
                      </div> */}
                    </Col>
                  </Row>
                </div>
              </Content>
            </Layout>
          </Layout>
        </Row>
      </>
    );
  }
}

export default HotlineCreation;
