import React, { Component } from "react";
import { Row, Col, Button, Divider, Timeline } from "antd";
import authHeader from "../../services/auth-header";
import { BASE_URL } from "../../userconfig";
import axios from "axios";
import moment from "moment"

const auditCode = {
  status:"1",
  urgency:"2",
  add:"3",
  view:"4"
};

const auditStatus={
  status:"Hotline status has been changed",
  urgency:"Hotline Urgency level has been changed",
  add:"Note Added",
  view:"View Hotline Converations"
}

class Activity extends Component {
  constructor() {
    super();
    this.state = {
      myclass: "",
      activity:[]
    };
    this.divclicked = this.divclicked.bind(this);
  }

  divclicked() {
    if (this.state.myclass === "") {
      this.setState({
        myclass: "rgt-panel",
      });
    } else {
      this.setState({
        myclass: "",
      });
    }
  }

  componentDidMount(){
    this.getAcitivity(this.props.conversation_id);
  }

  getAcitivity(conversationId){
    const token = authHeader();
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      }
    };

    axios
      .get(BASE_URL + "getconversationActivity/?conversation_id="+conversationId, options)
      .then((res) => {
        console.log("res", res)
        this.setState({activity:res.data})
      });
  }


  mapAuditLog(auditlogeventid){
    let str=""
    Object.keys(auditCode).map((key)=>{
      if(auditlogeventid === parseInt(auditCode[key])){
        console.log(auditStatus[key])
        str= auditStatus[key];
      }
    });
    return str;
  }


  render() {
    const {activity}=this.state;
    return (
      <>
        {/* <span onClick={this.divclicked} className="icon icon-create"></span> */}
        <div id="rgt-panel" className={this.props.class}>
          <p>
            <Row>
              <Col span={24}>
                <Row className="filter-wrapper align-items-center justify-content-between px-3 pt-3">
                  <Col span="12">
                    <h6>Activity</h6>
                  </Col>
                  <Button
                    onClick={this.showModal}
                    type="link"
                    className="link pl-0 pt-0 align-items-center d-flex"
                  >
                    <span className="icon icon-close small" onClick={this.props.activityClicked} />
                  </Button>
                </Row>
                <Divider className="my-2 w-100" />
                <Col span="24" className="px-3 pt-2">
                  <Timeline>
                   {activity.map((item)=>(
                      <Timeline.Item>
                      <span className="note-user">
                      {moment(item.created_on).format("DD MMM  hh:mm ")} {item.name}
                      </span>
                      <br />
                      {this.mapAuditLog(item.audit_log_event_id)}
                      {/* Updated the status to <b>Open</b> */}
                    </Timeline.Item>
                   ))}
                  </Timeline>
                  
                </Col>
              </Col>
            </Row>
          </p>
        </div>
      </>
    );
  }
}

export default Activity;