import React from "react";
import { Popover, Row, Col, Button, Divider, Checkbox } from "antd";

function onChange(checkedValues) {
  console.log("checked = ", checkedValues);
}
const urgencyOptions = [
  { label: "Low", value: "Low" },
  { label: "Medium", value: "Medium" },
  { label: "High", value: "High" },
];
const statusOptions = [
  { label: "New", value: "New" },
  { label: "Open", value: "Open" },
  { label: "Closed", value: "Closed" },
];
const hotlineOptions = [
  { label: "Microagressions", value: "Microagressions" },
  { label: "Misconduct", value: "Misconduct" },
];

const content = (
  <div>
    <Col span={24}>
      <Row className="filter-wrapper align-items-center px-3 py-2">
        <Col span="12">
          <h6>Filter</h6>
        </Col>
        <Col span="12" className="d-flex justify-content-end">
          <Button type="link" className="link pl-0 pt-0">
            Reset
          </Button>
        </Col>
      </Row>

      <Divider className="mt-0 mb-1 w-100" />
      <Row className="filter-wrapper align-items-center px-3 py-2">
        <div className="w-100 mb-4">
          <Col span="24" className="filter-type mb-3">
            <h6>Urgency Level</h6>
          </Col>
          <Col span="24">
            <Checkbox.Group
              className="d-flex flex-column flex-gap"
              options={urgencyOptions}
              defaultValue={["Low"]}
              onChange={onChange}
            />
          </Col>
        </div>

        <div className="w-100 mb-4">
          <Col span="24" className="filter-type mb-3">
            <h6>Status</h6>
          </Col>
          <Col span="24">
            <Checkbox.Group
              className="d-flex flex-column flex-gap"
              options={statusOptions}
              defaultValue={["Low"]}
              onChange={onChange}
            />
          </Col>
        </div>
        <div className="w-100 mb-4">
          <Col span="24" className="filter-type mb-3">
            <h6>Status</h6>
          </Col>
          <Col span="24">
            <Checkbox.Group
              className="d-flex flex-column flex-gap"
              options={hotlineOptions}
              defaultValue={["Low"]}
              onChange={onChange}
            />
          </Col>
        </div>
        <Row>
          <Col span="24" className="btn-row">
          <Button type="primary" className="mr-2">Apply</Button>
    <Button type="secondary">Cancel</Button>
          </Col>
        </Row>
      </Row>
    </Col>
  </div>
);

class FilterPopover extends React.Component {
  state = {
    visible: false,
  };

  hide = () => {
    this.setState({
      visible: false,
    });
  };

  handleVisibleChange = (visible) => {
    this.setState({ visible });
  };

  render() {
    return (
      <>
        <Popover
          placement="bottom"
          content={content}
          trigger="click"
          visible={this.state.visible}
          onVisibleChange={this.handleVisibleChange}
        >
          <span className="icon icon-filter"></span>
        </Popover>
      </>
    );
  }
}

export default FilterPopover;
